	import divider from '@/components/divider.vue';
	//引入网络请求
	import $H from '@/common/request.js';
	import $C from '@/common/config.js';
	import $U from '@/common/util.js';
	export default {
		components: {
			divider
		},
		data() {
			return {
				background: {
					backgroundColor: '#fff',
				},
				typeIndex: 1,
				value1: 1,
				value2: 1,
				sell: 0,
				noSell: 0,
				showPop: false,
				showPop2: false,
				selectIndex: 0,
				options1: [{
						label: '全部',
						value: 1,
					},
					{
						label: '上架中',
						value: 2,
					},
					{
						label: '已上架',
						value: 3,
					}
				],

				options3: [{
						label: '上架时间由近到远',
						value: 1,
					},
					{
						label: '销量多少',
						value: 2,
					}
				],
				page: 1,
				show: false,
				reload: true,
				loadMore: false,
				status: 'more',
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				},
				listData: [],
				is_put_shelves: "",
				is_asc: 1,
				nodata:false,
				scrollH:0,
				deleteId:""
			}
		},
		
		onShow() {
			this.reload = true;
			this.page = 1;
			this.status = 'more';
			this.getData()
		},

		onLoad() {
			
			uni.getSystemInfo({
				success: res => {
					this.scrollH = res.windowHeight - uni.upx2px(160)
				}
			});
			
			
		},

		onReachBottom() {
			this.loadMore = true;
			this.page++;
			if (this.status == 'noMore') {
				return;
			}
			this.status = 'loading';
			this.getData()
		},

		onPullDownRefresh() {
			this.reload = true;
			this.page = 1;
			this.status = 'more';
			this.getData()
		},

		methods: {

            toGoodsDetail(id){
				uni.navigateTo({
					url:'../createGoods/createGoods?id='+id
				})
			},

			changeState(index) {
				if (index == 1) {
					this.is_put_shelves = ""
				} else if (index == 2) {
					this.is_put_shelves = -1
				} else {
					this.is_put_shelves = 1
				}

				this.reload = true;
				this.page = 1;
				this.status = 'more';
				this.getData()
			},

			changeOrder(index) {
				console.log(index)
				this.is_asc = index

				this.reload = true;
				this.page = 1;
				this.status = 'more';
				this.getData()
			},

			chooseOption(index) {
				this.showPop = true
				this.selectIndex = index
			},

			deleteGoods(id) {
				this.showPop = false
				this.showPop2 = true
                this.deleteId=id
			},

			deleteConfirm() {
              // 发送到服务端
              $H.get("/goods/delete/"+ this.deleteId).then((res) => {
              	//请求成功				
              	console.log(res)
              	uni.showToast({
              		title:"删除成功",
              		icon:'success',
              		complete: () => {
              			setTimeout(() => {
              				this.reload = true;
              				this.page = 1;
              				this.status = 'more';
              				this.getData()
              			}, 1000)
              		}
              	})
              	
              }).catch((e) => {
              	
              	//请求失败
              	console.log("失败"+e)
              })
			},

			getData() {
				let data = {
					size: 10,
					current: this.page,
					is_asc:this.is_asc,
					is_put_shelves:this.is_put_shelves
				}

				// 发送到服务端
				$H.get("/goods/page/all", data).then((res) => {
					uni.stopPullDownRefresh()
					//请求成功
					console.log(res)
					let list = res.data.records

					let pages = res.data.pages;

					console.log(this.page)
					if (pages > 1 && this.page <= pages) {
						this.listData = this.reload ? list : this.listData.concat(list);
						this.nodata = false;
					} else if (pages == 0) {
						this.listData = [];
						this.nodata = true;
						this.status = 'noMore'

					} else if (pages == 1) {
						this.listData = list
						this.status = 'noMore'
						this.nodata = false;
					} else {
						this.status = 'noMore'
					}

					this.reload = false;


				}).catch((e) => {
					uni.stopPullDownRefresh()
					//请求失败
					console.log("失败" + e)
				})
			}


		}
	}
